import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Login from '../views/Login.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      {
        path: '/autoexam',
        name: 'autoexam',
        meta:{
          isShow: true,
          title: '算术自动出题'
        },
        component: () => import(/* webpackChunkName: "about" */ '../views/QuestionGenView.vue')
      },
      {
        path: '/questionbank',
        name: 'questionbank',
        meta:{
          isShow: true,
          title: '题库管理'
        },
        component: () => import(/* webpackChunkName: "about" */ '../views/QuestionBankView.vue')
      },
      {
        path: '/formula',
        name: 'formula',
        meta:{
          isShow: true,
          title: '公式大全'
        },
        component: () => import(/* webpackChunkName: "about" */ '../views/FormulaView.vue')
      },
      {
        path: '/wordproblem',
        name: 'wordproblem',
        meta:{
          isShow: true,
          title: '应用题'
        },
        component: () => import(/* webpackChunkName: "about" */ '../views/WordProblemView.vue')
      },
      {
        path: '/poetry',
        name: 'poetry',
        meta:{
          isShow: true,
          title: '古诗词'
        },
        component: () => import(/* webpackChunkName: "about" */ '../views/PoetryView.vue')
      },
      {
        path: '/copybook',
        name: 'copybook',
        meta:{
          isShow: true,
          title: '字帖'
        },
        component: () => import(/* webpackChunkName: "about" */ '../views/CopyBookView.vue')
      },
    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
