import service from ".";
import {QuestionQueryerInterface,QuestionInterface} from "../type/questionQueryer"
import {FormulaInterface,FormulaSearchInterface} from "@/type/formula"
import {PoetryInterface,PoetrySearchParam} from '@/type/poetry'
interface loginData{
    username:string,
    password:string
}



export function login(data:loginData){
    return service({
        url:"/p/60059944/admin/api/login",
        method:"post",
        data
    })

}

//算术题导出
export interface sumExportData{
    data:Array<QuestionInterface>
}
export function exportSumData(data:sumExportData){
    return service({
        url:"/sumquestion/export",
        method:"post",
        data,
        responseType:"blob",
        
    })
}

//问题管理

export function getQuestionList(params:QuestionQueryerInterface){
    return service({
        url:"/question/getlist",
        method:"get",
        params
    })
}

export function saveQuestion(data:QuestionInterface){
    return service({
        url:"/question/save",
        method:"post",
        data
    })
}



//公式管理
export function getFormulaList(params:FormulaSearchInterface){
    return service({
        url:"/formula/get",
        method:"get",
        params
    })
}

export function saveFormula(data:FormulaInterface){
    return service({
        url:"/formula/save",
        method:"post",
        data
    })
}

//古诗词管理

export function getPoetryList(params:PoetrySearchParam){
    return service({
        url:"/poetry/get",
        method:"get",
        params
    })
}
